.customize-control-aeon-resp-slider {
    .aeon-slider-buttons {
        display: flex;

        button.aeon-reset {
            cursor: pointer;
            background: none;
            border: none;
            opacity: .75;
            width: 100%;
            height: 100%;
            padding: 0;
            outline: none;

            span {
                font-size: 11px;
                line-height: 20px;
            }
        }
    }

    .components-base-control__field {
        margin: 0;
    }

    .aeon-slider-inputs-wrapper {
        display: flex;
        direction: ltr;

        .input-field-wrapper {
            display: none;
            width: 100%;

            &.active {
                display: block;
            }

            .components-input-control {
                width: 38px !important;
            }

            input {
                font-size: 11px;
                padding: 0;
                text-align: center;
                min-height: 30px;
                min-width: 38px;
                height: auto;
                border-radius: 0;
                border: 1px solid #ddd;
                z-index: 1;
                -moz-appearance: textfield;

                &:focus {
                    border-color: #2271b1;
                    outline: none;
                }

                &::-webkit-inner-spin-button,
                &::-webkit-outer-spin-button { 
                    -webkit-appearance: none;
                }
            }

            .components-input-control__backdrop {
                border: none;
                border-radius: 0;
                padding: 0;
            }
        }

        .ae-unit-picker__select {
            &.picker-inactive {
                pointer-events: none;
                opacity: 0.4;
            }

            .components-input-control__backdrop {
                border-color: #ddd;
                border-radius: 0;
                border-left-width: 0;
            }
        }
    }

    .components-select-control.ae-unit-picker__select {
        select {
            min-width: 50px;
            text-align: center;
        }
    }
}