.aeon-component-color-picker {
	.components-color-picker {
		box-sizing: border-box;
	}

	.components-color-picker__inputs-wrapper {
		display: none;
	}

	.react-colorful {
		width: 100% !important;

		.react-colorful__saturation {
			height: 150px;
		}

		.react-colorful__hue,
		.react-colorful__alpha {
			width: calc(100% - 40px);
		}

		& + div {
			display: none;
		}
	}

	.aeon-color-input-wrapper {
		display: flex;
		padding: 0;

		.aeon-color-input {
			flex-grow: 1;
		}

		.components-color-clear-color {
			margin-left: 5px;
		}

		input {
			margin-right: 5px;
		}

		.components-base-control__field {
			margin-bottom: 0;
		}
	}

	& > .components-base-control:first-child {
		margin-bottom: 0;
	}

	.components-color-picker__inputs-wrapper {
		min-width: auto;
	}

	.components-color-picker {
		padding: 5px;
		width: 100%;
	}

	.components-color-picker__body {
	    padding-bottom: 0;
	}

	.components-popover__content {
		padding: 10px;
		width: 295px;
		box-sizing: border-box;
		
		.components-base-control__label {
			display: none;
		}
	}

	.components-color-clear-color {
		height: auto;
	}

	.components-circular-option-picker__option.is-pressed + svg {
		fill: #fff;
		background: #000;
		transform: scale(0.6);
	}
}

.aeon-customize-control-wrapper {
	display: flex;

	&.aeon-customize-color-control-wrapper {
		& > div:first-child {
			flex-grow: 1;
		}

		& > div:not(:last-child) {
			margin-right: 5px;
		}
	}
}

.aeon-component-color-picker-wrapper {
    > .components-base-control__field {
		position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
		margin: 0;

        .components-color-palette__item-wrapper,
		.customize-control-title {
            margin: 0;
        }
	}
}

.aeon-color-picker-area {
	button {
		width: 28px;
		height: 28px;
		position: relative;

		&:hover {
			background-color: inherit;
		}

		&:focus:after {
			width: 28px;
			height: 28px;
		}
	}
}

.aeon-customize-control--popover {
	.components-color-picker {
		box-sizing: border-box;
	}

	.components-color-picker__inputs-wrapper {
		display: none;
	}

	& > .components-base-control:first-child {
		margin-bottom: 0;
	}

	.components-color-picker__inputs-wrapper {
		min-width: auto;
	}

	.components-color-picker {
		padding: 5px;
	}

	.components-color-picker__body {
	    padding-bottom: 0;
	}

	.components-color-clear-color {
		height: auto;
	}
}

.aeon-color-option-area {
    padding: 16px 16px 0;
}

/* RTL */
body.rtl .aeon-component-color-picker {
	.aeon-color-input-wrapper {
		.components-color-clear-color {
			margin-left: 0;
			margin-right: 5px;
		}

		input {
			margin-right: 0;
			margin-left: 5px;
		}
	}
}

body.rtl .aeon-customize-control-wrapper.aeon-customize-color-control-wrapper > div:not(:last-child) {
	margin-right: 0;
	margin-left: 5px;
}