.customize-control-aeon-sortable .sortable {
    cursor: default;

    .dashicons-visibility {
        cursor: pointer;
    }
    
    div {
        padding: 0 0 0 12px;
        border: 1px solid #dddddd;
        color: #556068;
        background: #ffffff;
        cursor: move;
        line-height: 38px;
        margin: 0 0 10px;
        border-radius: 3px;

        &:last-child {
            margin: 0;
        }
        
        .dashicons.visibility {
            float: right;
            position: relative;
            font-size: 18px;
            padding: 9px 12px;
            line-height: 20px;
            border-width: 0 0 0 1px;
            border-style: solid;
            border-color: #ddd;
        }
        
        &.invisible {
            opacity: 0.6;

            .dashicons.visibility:after {
                opacity: 1;
                position: absolute;
                content: '';
                width: 20px;
                height: 2px;
                top: 18px;
                left: 12px;
                background: #656565;
                border-radius: 5px;
                transform: rotate(-45deg);
                box-shadow: 0px 0px 0px 1px #fff;
                transition: opacity 0.2s ease;
            }
        }
        
        &:hover {
            opacity: 0.8;
        }
    }
}

/* RTL */
body.rtl .customize-control-aeon-sortable .sortable div {
    padding: 0 12px 0 0;
    
    .dashicons.visibility {
        float: left;
        border-width: 0 1px 0 0;
    }
    
    &.invisible {
        .dashicons.visibility:after {
            left: auto;
            right: 12px;
        }
    }
}