.customize-control-aeon-radio-image .aeon-radio-images {
    display: flex;
    align-items: center;
    justify-content: space-between;

    input {
        display: none;

        &:checked + label svg {
            .out {
                fill: #2271b1;
                stroke: #2271b1;
            }

            .fw-out {
                stroke: #2271b1;
            }

            .in {
                fill: #fff;
            }

            .fw-in {
                fill: #2271b1;
            }
        }
    }

    svg {
        width: 62px;
        vertical-align: middle;
    }
}