#customize-controls .customize-control-notifications-container {
    margin: 0;
}

.components-base-control__help {
    margin-top: 2px;
	margin-bottom: 0;
}

.components-base-control__label {
	display: block;
	margin-bottom: 10px;
}

.aeon-customize-control-wrapper {
	display: flex;
}

.aeon-customize-control-wrapper[data-wrapper-type="color"] {
	& > div:first-child {
		flex-grow: 1;
	}

	& > div:not(:first-child):not(:empty) {
		margin-left: 5px;
	}
}

.aeon-customize-control-wrapper[data-wrapper-type="two-col"] {
	& > div {
    	flex-basis: calc(50% - 5px);

		&:nth-child(odd) {
			margin-right: 5px;
		}

		&:nth-child(even) {
			margin-left: 5px;
		}
	}
}

.aeon-customize-control--popover {
	& > .components-popover__content {
		padding: 15px;
		width: 280px;
		box-sizing: border-box;
	}

	.components-base-control:not(:last-child) {
		margin-bottom: 15px;
	}
}

.customize-control-title {
    font-size: 13px;
    color: #3c434a;
}

.wp-full-overlay-sidebar {
    width: 300px;
}

@media screen and (min-width: 1667px) {
    .wp-full-overlay.expanded {
        margin-left: 300px;
    }
}

.expanded .wp-full-overlay-footer {
    width: 300px;
}

.customize-control {
    padding-top: 20px;
    margin-top: 20px;
    border-top: 1px solid #ddd;
    margin-bottom: 0;

	&:nth-child(2) {
		padding-top: 0;
		margin-top: 0;
		border-top: 0;
	}
	
	&.no-separator {
		padding: 0;
		margin: 0;
		border: 0;
	}
	
	&.typo-range {
		margin-top: 12px;
	}
	
	&.custom-heading {
		margin-top: 30px;
	}
	
	&.nom {
		margin: 0;
	}
	
	.aeon-control-title {
		display: block;
		background-color: #fff;
		color: #222;
		padding: 6px 10px;
		font-size: 14px;
		line-height: 1.5;
		font-weight: 600;
		border-top: 2px solid #017bba;
		margin-bottom: 20px;
	}
}

.aeon-title-area {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 5px;

	.customize-control-title {
		margin: 0;
	}

	.aeon-device-controls {
		display: flex;
    	align-items: center;

		li {
			margin: 0;

			button {
				cursor: pointer;
				background: none;
				border: none;
				opacity: .75;
				width: 100%;
				height: 100%;
				padding: 0;
				outline: none;
			
				&:hover {
					color: #017bba;
				}

				> i {
					font-size: 11px;
					line-height: 20px;
					vertical-align: middle;
				}
			}
			
			&.active button {
				color: #017bba;
			}
		}
	}
}

.inline {
	&.customize-control-aeon-select {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	
		.customize-control-title {
			margin: 0;
		}
	
		.customize-control-content {
			max-width: 400px;
			width: 52%;
		}
	}

    .aeon-text-wrapper {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		.customize-control-title {
			margin: 0;
		}

		.components-base-control {
			max-width: 400px;
			width: 52%;
	
			.components-base-control__field {
				margin: 0;
			}
		}
	}
}

/* RTL */
body.rtl .aeon-customize-control-wrapper[data-wrapper-type="color"] > div:not(:first-child):not(:empty) {
	margin-left: 0;
	margin-right: 5px;
}

body.rtl .aeon-customize-control-wrapper[data-wrapper-type="two-col"] > div {
	&:nth-child(odd) {
		margin-right: 0;
		margin-left: 5px;
	}

	&:nth-child(even) {
		margin-left: 0;
		margin-right: 5px;
	}
}

@media screen and (min-width: 1667px) {
    body.rtl .wp-full-overlay.expanded {
        margin-left: 0;
        margin-right: 300px;
    }
}