.customize-control-aeon-spacing {
    .aeon-spacing-wrapper {
        display: none;

        li {
            margin: 0;
            text-align: center;
            margin-right: 2px;

            &:last-child {
                margin: 0;
            }

            input {
                display: block;
                font-size: 12px;
                min-height: 30px;
                padding: 0 5px;
                border-color: #ddd;
                border-radius: 3px;
                box-shadow: none;
                transition: all .3s;
                text-align: center;
                -moz-appearance: textfield;

                &:focus {
                    box-shadow: none;
                    outline: none;
                    border-color: #2271b1;
                }

                &::-webkit-inner-spin-button,
                &::-webkit-outer-spin-button { 
                    -webkit-appearance: none;
                }
            }

            span {
                text-transform: uppercase;
                font-size: 10px;
                opacity: .75;
            }
        }

        .aeon-spacing-input-item-link {
            cursor: pointer;

            span {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 1px 0;
                font-size: 12px;
                width: 35px;
                height: 26px;
                border: 1px solid #ddd;
                background-color: #fff;
                color: #1e8cbe;
                border-radius: 3px;
            }
        
            .aeon-spacing-connected,
            &.disconnected .aeon-spacing-disconnected {
                display: none;
            }
        
            &.disconnected .aeon-spacing-connected {
                display: flex;
                background-color: #a4afb7;
                border-color: #a4afb7;
                color: #fff;
            }
        }
    }

    .aeon-spacing-wrapper.active {
        display: flex;
    }
}

/* RTL */
body.rtl .customize-control-aeon-spacing .aeon-spacing-wrapper li {
    margin-right: 0;
    margin-left: 2px;

    &:last-child {
        margin: 0;
    }
}