.customize-control-aeon-selector {
    .aeon-alignment-wrapper {
        display: flex;
    }

    .aeon-selector-responsive-wrap {
        display: none;

        &.active {
            display: flex;
            flex: 1 1 0;
        }
    }

    .aeon-alignment-inner-wrap {
        display: flex;
        flex: 1 1 0;
    }

    button {
        background: none;
        outline: unset;
        border-width: 1px;
        border-style: solid;
        border-color: #ddd;
        width: 100%;
        height: 100%;
        border-radius: 0;
        text-align: center;
        justify-content: center;
        transition: none;

        &:hover {
            color: #222;
        }

        &:focus:not(:disabled) {
            box-shadow: none;
        }

        &.is-primary {
            border-color: #2271b1;

            &:hover:not(:disabled) {
                background: #2271b1;
            }

            &:focus:not(:disabled) {
                box-shadow: none;
            }
        }

        svg {
            width: 1em;
            height: 1em;
        }
    }

    .aeon-alignment-inner-wrap {
        &:first-child button {
            border-radius: 2px 0 0 2px;
        }

        &:last-child button {
            border-radius: 0 2px 2px 0;
        }

        &:not(:last-child) button {
            border-right-width: 0;
        }
    }
}

/* RTL */
body.rtl .customize-control-aeon-selector {
    .aeon-selector-responsive-wrap {
        display: none;

        &.active,
        .aeon-alignment-inner-wrap {
            display: flex;
            flex: 1 1 0;
        }
    }

    .aeon-alignment-inner-wrap {
        &:first-child button {
            border-radius: 0 2px 2px 0;
        }

        &:last-child button {
            border-radius: 2px 0 0 2px;
        }

        &:not(:last-child) button {
            border-left-width: 0;
            border-right-width: 1px;
        }
    }
}