.customize-control-aeon-font-family {
    .react-select__control {
        min-height: 30px;
        border-color: #8c8f94;
        cursor: pointer;

        .react-select__value-container {
            padding-top: 0;
            padding-bottom: 0;
        }

        .react-select__input input:focus {
            box-shadow: none;
        }
    }

    .react-select__menu {
        z-index: 9;
    }

	.ae-typo-weight-transform {
		display: flex;
		justify-content: space-between;
		margin-top: 10px;

		> div {
			width: 48%;
		}
		
		label {
			color: #3c434a;
			font-weight: 600;
		}

		.components-base-control__field .components-input-control__backdrop {
			border-color: #8c8f94;
		}
	}
}