.customize-control-aeon-social {
    .dashicon {
        font-size: 16px;
        width: 16px;
        height: 16px;
    }
}

.aeon-sorter-item {
    background: #fff;
    position: relative;
    border: 1px solid #ddd;
    white-space: nowrap;
    margin-bottom: 10px;
    border-radius: 3px;

    &.is-disabled {
        opacity: .6;
    }

    .aeon-sorter-item-panel-header {
        display: flex;
        width: 100%;
        cursor: pointer;
        align-items: center;
    }

    .aeon-sorter-item-expand {
        position: relative;
        transition: .3s;
        color: #72777c;
        border-right: 1px solid #ddd;

        &:focus:not(:disabled) {
            box-shadow: none;
            outline: none;
        }

        &:before {
            opacity: 0;
            position: absolute;
            content: '';
            width: 15px;
            height: 2px;
            background: #656565;
            border-radius: 5px;
            transform: rotate(-45deg);
            box-shadow: 0px 0px 0px 1px #fff;
            transition: opacity 0.1s ease;
        }

        &:hover:before,
        &.item-is-hidden:before {
            opacity: 1;
        }
    }

    .aeon-sorter-title {
        flex-grow: 2;
        padding: 0 6px;
        color: #556068;
    }
    
    .aeon-sorter-item-remove {
        color: #72777c;
        border-left: 1px solid #ddd;
        box-shadow: none;

        &.is-destructive {
            &:hover:not(:disabled),
            &:active:not(:disabled),
            &:focus:not(:disabled) {
                background: transparent;
                color: #dd4b39;
                box-shadow: none;
                outline: none;
            }
        }
    }

    .aeon-sorter-item-panel-content {
        padding: 10px;
        border-top: 1px solid #ddd;

        .components-base-control__field {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
        }

        .components-base-control__label {
            margin: 0;
        }

        input[type="text"] {
            max-width: 400px;
            width: 65%;
        }
    }
}

.aeon-social-add-area {
    display: flex;
    align-items: center;

    .components-base-control {
        width: 100%;

        .components-base-control__field {
            margin: 0;
    
            .components-input-control__backdrop {
                border: 1px solid #ddd;
            }
        }
    }

    .aeon-sorter-add-item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 42px;
        height: 30px;
        margin-left: 7px;
    }
}